'use client';

import { ArrowRight } from 'lucide-react';
import type { SummaryParams } from '@/lib/ai/tools';

interface SummaryCardProps extends SummaryParams {
  onContinue?: () => void;
}

export function SummaryCard({ title, sections, onContinue }: SummaryCardProps) {
  return (
    <div className="card summary-card">
      <h3 className="summary-title">{title}</h3>

      <div className="summary-sections">
        {sections.map((section, index) => (
          <div key={index} className="summary-section">
            <h4 className="summary-section-heading">
              {section.heading}
            </h4>
            <div className="summary-section-content">
              {section.content}
            </div>
          </div>
        ))}
      </div>

      {onContinue && (
        <div className="card-footer">
          <button onClick={onContinue} className="btn btn-primary">
            Continue <ArrowRight className="icon-sm" />
          </button>
        </div>
      )}
    </div>
  );
}
