'use client';

import { useState } from 'react';
import { Target, Zap, Rocket, Check, ArrowRight } from 'lucide-react';
import type { OpportunityParams } from '@/lib/ai/tools';

interface OpportunityCardProps extends OpportunityParams {
  onSelect?: (selectedIds: string[]) => void;
  onContinue?: () => void;
}

export function OpportunityCard({
  tactical,
  strategic,
  onSelect,
  onContinue,
}: OpportunityCardProps) {
  const [selected, setSelected] = useState<string[]>([]);

  const toggleSelect = (id: string) => {
    setSelected((prev) =>
      prev.includes(id) ? prev.filter((i) => i !== id) : [...prev, id]
    );
  };

  const getEffortClass = (effort: 'Low' | 'Medium' | 'High') => {
    switch (effort) {
      case 'Low':
        return 'opportunity-effort-low';
      case 'Medium':
        return 'opportunity-effort-medium';
      case 'High':
        return 'opportunity-effort-high';
    }
  };

  const renderOpportunity = (
    opp: (typeof tactical)[0] | (typeof strategic)[0],
    type: 'tactical' | 'strategic'
  ) => (
    <div
      key={opp.id}
      onClick={() => toggleSelect(opp.id)}
      className={`opportunity-item ${
        selected.includes(opp.id) ? 'opportunity-item-selected' : ''
      }`}
    >
      <div className="opportunity-item-content">
        <div className="opportunity-item-main">
          <div className="opportunity-item-header">
            <h4 className="opportunity-item-name">{opp.name}</h4>
            <span className={`opportunity-effort ${getEffortClass(opp.effort)}`}>
              {opp.effort} Effort
            </span>
          </div>
          <p className="opportunity-item-description">{opp.description}</p>
          <div className="opportunity-item-meta">
            <span className="opportunity-timeline">
              <strong>{opp.timeline}</strong>
            </span>
            <span className="opportunity-savings">
              {opp.estimatedSavings}
            </span>
          </div>
          {'prerequisites' in opp && opp.prerequisites && (
            <div className="opportunity-prerequisites">
              Prerequisites: {opp.prerequisites.join(', ')}
            </div>
          )}
        </div>
        <div
          className={`opportunity-checkbox ${
            selected.includes(opp.id) ? 'opportunity-checkbox-checked' : ''
          }`}
        >
          {selected.includes(opp.id) && (
            <Check className="icon-sm" />
          )}
        </div>
      </div>
    </div>
  );

  return (
    <div className="card opportunity-card">
      <div className="opportunity-header">
        <Target className="icon-lg opportunity-icon" />
        <h3 className="opportunity-title">AI Opportunities for Your Operation</h3>
      </div>
      <p className="opportunity-subtitle">
        Select the opportunities you'd like to prioritize. Click to select/deselect.
      </p>

      {tactical.length > 0 && (
        <div className="opportunity-section">
          <h4 className="opportunity-section-title">
            <Zap className="icon-md opportunity-section-icon-tactical" />
            Tactical Wins (30-90 days)
          </h4>
          <div className="opportunity-list">
            {tactical.map((opp) => renderOpportunity(opp, 'tactical'))}
          </div>
        </div>
      )}

      {strategic.length > 0 && (
        <div className="opportunity-section">
          <h4 className="opportunity-section-title">
            <Rocket className="icon-md opportunity-section-icon-strategic" />
            Strategic Initiatives (6-18 months)
          </h4>
          <div className="opportunity-list">
            {strategic.map((opp) => renderOpportunity(opp, 'strategic'))}
          </div>
        </div>
      )}

      {selected.length > 0 && (
        <div className="opportunity-selection-count">
          <strong>{selected.length}</strong> opportunities selected
        </div>
      )}

      <div className="opportunity-actions">
        {onSelect && selected.length > 0 && (
          <button
            onClick={() => onSelect(selected)}
            className="btn btn-primary"
          >
            Continue with Selected <ArrowRight className="icon-sm" />
          </button>
        )}
        {onContinue && (
          <button
            onClick={onContinue}
            className="btn btn-secondary"
          >
            Continue
          </button>
        )}
      </div>
    </div>
  );
}
