'use client';

import { useState } from 'react';
import { Check, ArrowRight } from 'lucide-react';
import type { QuestionParams } from '@/lib/ai/tools';

interface QuestionCardProps extends QuestionParams {
  onSelect: (selectedIds: string[]) => void;
  disabled?: boolean;
}

export function QuestionCard({
  header,
  question,
  inputType = 'select',
  placeholder,
  options = [],
  allowMultiple = false,
  onSelect,
  disabled = false,
}: QuestionCardProps) {
  const [selected, setSelected] = useState<string[]>([]);
  const [textInput, setTextInput] = useState('');
  const [showOther, setShowOther] = useState(false);

  const handleOptionClick = (optionId: string) => {
    if (disabled) return;

    if (optionId === 'other') {
      setShowOther(true);
      return;
    }

    if (allowMultiple) {
      setSelected((prev) =>
        prev.includes(optionId)
          ? prev.filter((id) => id !== optionId)
          : [...prev, optionId]
      );
    } else {
      // Single select - submit immediately
      onSelect([optionId]);
    }
  };

  const handleSubmitMultiple = () => {
    if (selected.length > 0) {
      onSelect(selected);
    }
  };

  const handleSubmitText = () => {
    if (textInput.trim()) {
      onSelect([textInput.trim()]);
    }
  };

  // Text input mode
  if (inputType === 'text') {
    return (
      <div className="card question-card">
        <div className="question-card-header">
          <span className="question-card-label">{header}</span>
        </div>
        <h3 className="question-card-title">{question}</h3>

        <div className="question-card-text-input">
          <input
            type="text"
            value={textInput}
            onChange={(e) => setTextInput(e.target.value)}
            placeholder={placeholder || 'Type your answer...'}
            className="input"
            autoFocus
            disabled={disabled}
            onKeyDown={(e) => {
              if (e.key === 'Enter' && textInput.trim()) {
                handleSubmitText();
              }
            }}
          />
          <button
            onClick={handleSubmitText}
            disabled={!textInput.trim() || disabled}
            className="btn btn-primary"
          >
            Continue <ArrowRight className="icon-sm" />
          </button>
        </div>
      </div>
    );
  }

  // Select mode (multiple choice)
  return (
    <div className="card question-card">
      <div className="question-card-header">
        <span className="question-card-label">{header}</span>
      </div>
      <h3 className="question-card-title">{question}</h3>

      {showOther ? (
        <div className="question-card-other">
          <input
            type="text"
            value={textInput}
            onChange={(e) => setTextInput(e.target.value)}
            placeholder="Type your answer..."
            className="input"
            autoFocus
            onKeyDown={(e) => {
              if (e.key === 'Enter' && textInput.trim()) {
                handleSubmitText();
              }
            }}
          />
          <div className="question-card-actions">
            <button
              onClick={handleSubmitText}
              disabled={!textInput.trim()}
              className="btn btn-primary"
            >
              Submit
            </button>
            <button
              onClick={() => setShowOther(false)}
              className="btn btn-ghost"
            >
              Back
            </button>
          </div>
        </div>
      ) : (
        <div className="question-card-options">
          {options.map((option) => (
            <button
              key={option.id}
              onClick={() => handleOptionClick(option.id)}
              disabled={disabled}
              className={`question-option ${
                selected.includes(option.id) ? 'question-option-selected' : ''
              } ${disabled ? 'question-option-disabled' : ''}`}
            >
              <div className="question-option-content">
                {allowMultiple && (
                  <div
                    className={`question-checkbox ${
                      selected.includes(option.id) ? 'question-checkbox-checked' : ''
                    }`}
                  >
                    {selected.includes(option.id) && (
                      <Check className="icon-sm" />
                    )}
                  </div>
                )}
                <div className="question-option-text">
                  <div className="question-option-label">{option.label}</div>
                  {option.description && (
                    <div className="question-option-description">
                      {option.description}
                    </div>
                  )}
                </div>
              </div>
            </button>
          ))}

          {/* Other option */}
          <button
            onClick={() => handleOptionClick('other')}
            disabled={disabled}
            className={`question-option question-option-other ${
              disabled ? 'question-option-disabled' : ''
            }`}
          >
            <div className="question-option-label">Other...</div>
            <div className="question-option-description">Type a custom answer</div>
          </button>

          {allowMultiple && selected.length > 0 && (
            <button
              onClick={handleSubmitMultiple}
              className="btn btn-primary question-submit-btn"
            >
              Continue with {selected.length} selected
            </button>
          )}
        </div>
      )}
    </div>
  );
}
