'use client';

import { useState } from 'react';
import { Check, ChevronDown, ChevronUp, ArrowRight } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import type { EducationParams } from '@/lib/ai/tools';

interface EducationContentProps extends EducationParams {
  onDrillDown?: (topic: string) => void;
  onContinue?: () => void;
}

export function EducationContent({
  title,
  content,
  keyPoints,
  drillDownOptions,
  onDrillDown,
  onContinue,
}: EducationContentProps) {
  const [expanded, setExpanded] = useState(false);
  const displayContent = expanded ? content : content.slice(0, 500) + (content.length > 500 ? '...' : '');

  return (
    <div className="card education-card">
      <h3 className="education-title">{title}</h3>

      <div className="education-content">
        <div className="education-text markdown-content">
          <ReactMarkdown>{displayContent}</ReactMarkdown>
        </div>

        {content.length > 500 && (
          <button
            onClick={() => setExpanded(!expanded)}
            className="education-toggle"
          >
            {expanded ? (
              <>Show less <ChevronUp className="icon-sm" /></>
            ) : (
              <>Read more <ChevronDown className="icon-sm" /></>
            )}
          </button>
        )}
      </div>

      {keyPoints && keyPoints.length > 0 && (
        <div className="education-section">
          <h4 className="education-section-title">Key Takeaways</h4>
          <ul className="education-key-points">
            {keyPoints.map((point, index) => (
              <li key={index} className="education-key-point">
                <span className="education-key-point-icon">
                  <Check className="icon-sm" />
                </span>
                <span className="education-key-point-text markdown-content">
                  <ReactMarkdown>{point}</ReactMarkdown>
                </span>
              </li>
            ))}
          </ul>
        </div>
      )}

      {drillDownOptions && drillDownOptions.length > 0 && onDrillDown && (
        <div className="education-section">
          <h4 className="education-section-title">Want to go deeper?</h4>
          <div className="education-drilldown-options">
            {drillDownOptions.map((option, index) => (
              <button
                key={index}
                onClick={() => onDrillDown(option)}
                className="btn btn-secondary education-drilldown-btn"
              >
                {option}
              </button>
            ))}
          </div>
        </div>
      )}

      {onContinue && (
        <div className="education-section">
          <button onClick={onContinue} className="btn btn-primary">
            Continue <ArrowRight className="icon-sm" />
          </button>
        </div>
      )}
    </div>
  );
}
