'use client';

import { Calendar, Target, ArrowRight } from 'lucide-react';
import type { RoadmapParams } from '@/lib/ai/tools';

interface RoadmapTimelineProps extends RoadmapParams {
  onContinue?: () => void;
}

export function RoadmapTimeline({ quarters, onContinue }: RoadmapTimelineProps) {
  return (
    <div className="card roadmap-card">
      <div className="roadmap-header">
        <Calendar className="icon-lg roadmap-icon" />
        <h3 className="roadmap-title">Implementation Roadmap</h3>
      </div>
      <p className="roadmap-subtitle">
        A phased approach to maximize success and minimize risk.
      </p>

      <div className="roadmap-timeline">
        {quarters.map((quarter, qIndex) => (
          <div key={qIndex} className="roadmap-quarter">
            {/* Timeline connector */}
            {qIndex < quarters.length - 1 && (
              <div className="roadmap-connector" />
            )}

            <div className="roadmap-quarter-content">
              {/* Quarter indicator */}
              <div className="roadmap-quarter-indicator">
                {quarter.quarter}
              </div>

              {/* Quarter content */}
              <div className="roadmap-quarter-body">
                <h4 className="roadmap-quarter-title">
                  {quarter.title}
                </h4>
                <div className="roadmap-initiatives">
                  {quarter.initiatives.map((init, iIndex) => (
                    <div key={iIndex} className="roadmap-initiative">
                      <div className="roadmap-initiative-name">
                        {init.name}
                      </div>
                      <div className="roadmap-initiative-description">
                        {init.description}
                      </div>
                      {init.milestone && (
                        <div className="roadmap-milestone">
                          <Target className="icon-sm" />
                          Milestone: {init.milestone}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {onContinue && (
        <div className="card-footer">
          <button onClick={onContinue} className="btn btn-primary">
            Continue <ArrowRight className="icon-sm" />
          </button>
        </div>
      )}
    </div>
  );
}
