'use client';

import { BarChart3, Lightbulb, ArrowRight } from 'lucide-react';
import type { BenchmarkParams } from '@/lib/ai/tools';

interface BenchmarkChartProps extends BenchmarkParams {
  onContinue?: () => void;
}

export function BenchmarkChart({
  industry,
  metrics,
  insights,
  onContinue,
}: BenchmarkChartProps) {
  return (
    <div className="card benchmark-card">
      <div className="benchmark-header">
        <BarChart3 className="icon-lg benchmark-icon" />
        <h3 className="benchmark-title">
          {industry} Industry Benchmarks
        </h3>
      </div>

      <div className="benchmark-metrics">
        {metrics.map((metric, index) => (
          <div key={index} className="benchmark-metric">
            <div className="benchmark-metric-name">{metric.name}</div>
            <div className="benchmark-metric-value">{metric.value}</div>
            {metric.context && (
              <div className="benchmark-metric-context">{metric.context}</div>
            )}
          </div>
        ))}
      </div>

      {insights && insights.length > 0 && (
        <div className="benchmark-insights">
          <h4 className="benchmark-insights-title">
            <Lightbulb className="icon-sm" />
            Did You Know?
          </h4>
          <ul className="benchmark-insights-list">
            {insights.map((insight, index) => (
              <li key={index} className="benchmark-insight">
                {insight}
              </li>
            ))}
          </ul>
        </div>
      )}

      {onContinue && (
        <div className="card-footer">
          <button onClick={onContinue} className="btn btn-primary">
            Continue <ArrowRight className="icon-sm" />
          </button>
        </div>
      )}
    </div>
  );
}
