'use client';

import { useChat } from '@ai-sdk/react';
import { useEffect, useRef, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { Send, RotateCcw } from 'lucide-react';
import Image from 'next/image';
import {
  ChatMessage,
  QuestionCard,
  EducationContent,
  BenchmarkChart,
  OpportunityCard,
  RoadmapTimeline,
  SummaryCard,
  LoadingSpinner,
} from './ui';
import type {
  QuestionParams,
  EducationParams,
  BenchmarkParams,
  OpportunityParams,
  RoadmapParams,
  SummaryParams,
} from '@/lib/ai/tools';

interface ToolInvocation {
  toolCallId: string;
  toolName: string;
  args: Record<string, unknown>;
  state: 'partial-call' | 'call' | 'result';
  result?: unknown;
}

interface Message {
  id: string;
  role: 'user' | 'assistant' | 'system' | 'data' | 'function' | 'tool';
  content: string;
  toolInvocations?: ToolInvocation[];
}

export function Chat() {
  const [sessionId] = useState(() => {
    if (typeof window !== 'undefined') {
      const stored = localStorage.getItem('cs-advisor-session-id');
      if (stored) return stored;
      const newId = uuidv4();
      localStorage.setItem('cs-advisor-session-id', newId);
      return newId;
    }
    return uuidv4();
  });

  const { messages, input, handleInputChange, handleSubmit, isLoading, append } =
    useChat({
      api: '/api/chat',
      body: { sessionId },
      initialMessages: [],
    });

  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom on new messages
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Start conversation on mount - trigger upfront discovery
  useEffect(() => {
    if (messages.length === 0) {
      append({
        role: 'user',
        content: 'Start the discovery conversation.',
      });
    }
  }, []);

  // Handle question selection
  const handleQuestionSelect = (selectedIds: string[]) => {
    const response = selectedIds.join(', ');
    append({
      role: 'user',
      content: response,
    });
  };

  // Handle drill-down selection
  const handleDrillDown = (topic: string) => {
    append({
      role: 'user',
      content: `Tell me more about: ${topic}`,
    });
  };

  // Handle opportunity selection
  const handleOpportunitySelect = (ids: string[]) => {
    append({
      role: 'user',
      content: `I'm interested in these opportunities: ${ids.join(', ')}`,
    });
  };

  // Handle continue actions
  const handleContinue = () => {
    append({
      role: 'user',
      content: 'Continue',
    });
  };

  // Render tool call component based on tool name and args
  const renderToolCall = (invocation: ToolInvocation) => {
    if (invocation.state === 'partial-call') {
      return <LoadingSpinner />;
    }

    const { toolName, args } = invocation;

    switch (toolName) {
      case 'showQuestion':
        return (
          <QuestionCard
            {...(args as QuestionParams)}
            onSelect={handleQuestionSelect}
          />
        );

      case 'showEducation':
        return (
          <EducationContent
            {...(args as EducationParams)}
            onDrillDown={handleDrillDown}
            onContinue={handleContinue}
          />
        );

      case 'showBenchmarks':
        return (
          <BenchmarkChart
            {...(args as BenchmarkParams)}
            onContinue={handleContinue}
          />
        );

      case 'showOpportunities':
        return (
          <OpportunityCard
            {...(args as OpportunityParams)}
            onSelect={handleOpportunitySelect}
          />
        );

      case 'showRoadmap':
        return (
          <RoadmapTimeline
            {...(args as RoadmapParams)}
            onContinue={handleContinue}
          />
        );

      case 'showSummary':
        return (
          <SummaryCard
            {...(args as SummaryParams)}
            onContinue={handleContinue}
          />
        );

      case 'updateContext':
        return null;

      default:
        return null;
    }
  };

  return (
    <div className="chat-container">
      {/* Header */}
      <header className="chat-header">
        <div className="chat-header-content">
          <div className="chat-header-brand">
            <Image
              src="/synaptic-icon.svg"
              alt="Synaptic"
              width={40}
              height={40}
              className="chat-header-logo"
            />
            <div>
              <h1 className="chat-header-title">CS Advisor</h1>
              <p className="chat-header-subtitle">
                AI Opportunity Discovery for Customer Service Leaders
              </p>
            </div>
          </div>
          <button
            onClick={() => {
              localStorage.removeItem('cs-advisor-session-id');
              window.location.reload();
            }}
            className="btn btn-ghost chat-new-session"
          >
            <RotateCcw className="icon-sm" />
            New Session
          </button>
        </div>
      </header>

      {/* Messages */}
      <div className="chat-messages">
        <div className="chat-messages-content">
          {messages.map((message: Message) => (
            <div key={message.id}>
              {/* Regular message content */}
              {message.content && (
                <ChatMessage role={message.role as 'user' | 'assistant'}>
                  {message.content}
                </ChatMessage>
              )}

              {/* Tool invocations */}
              {message.toolInvocations?.map((invocation) => (
                <div key={invocation.toolCallId} className="chat-tool-result">
                  {renderToolCall(invocation)}
                </div>
              ))}
            </div>
          ))}

          {/* Loading indicator */}
          {isLoading && <LoadingSpinner />}

          <div ref={messagesEndRef} />
        </div>
      </div>

      {/* Input */}
      <div className="chat-input-container">
        <div className="chat-input-content">
          <form onSubmit={handleSubmit} className="chat-input-form">
            <input
              type="text"
              value={input}
              onChange={handleInputChange}
              placeholder="Type your message..."
              className="input chat-input"
              disabled={isLoading}
            />
            <button
              type="submit"
              disabled={isLoading || !input.trim()}
              className="btn btn-primary chat-send-btn"
            >
              <Send className="icon-sm" />
              Send
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
